<template>
  <div class="coupons">
    <div class="main" v-for="(coupon, index) in exchanges" :key="index">
      <div class="title">{{ coupon.rulename }}</div>
      <div class="content">
        <div class="couponsText">
          <p
            v-for="(item, index) in coupon.ladderVOS"
            :key="index"
          >满{{ item.ladamt }}元{{ coupon.reduce }}{{ item.offers }}{{ coupon.symbol }}</p>
          <!-- <p>需积分兑换</p> -->
          <p
            class="time"
          >需 {{coupon.ladderVOS[0].offer * 1000}} 积分兑换</p>
        </div>
        <div class="btn" @click="receive(coupon)">{{ btn }}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["exchanges", "btn"],
  methods: {
    receive(exchange) {
      if (this.btn === "立即兑换") {
        JNB.query({
          serverName: "orderServer"+this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls: "CouponRevModule",
          method: "revExcgCoupon",
          params: exchange,
          callback: data => {
            let result = JSON.parse(data);
            if (result.code === 200) {
              this.$emit('exchangeFlag',true)
              this.$toast("兑换成功！");
            }else{
              this.$toast(result.message);
            }
          }
        });
      }
    }
  }
};
</script>
<style scoped>
.coupons .main {
  width: 715px;
  height: 152px;
  overflow: hidden;
  margin: 20px auto;
}
.coupons .main .title {
  width: 230px;
  height: 152px;
  background: url("../assets/me_icon/coupon.png") no-repeat;
  background-size: contain;
  font-size: 32px;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  text-align: center;
  line-height: 152px;
  float: left;
}
.coupons .main .content {
  width: 480px;
  height: 148px;
  border: 1px solid rgba(230, 230, 230, 1);
  border-left: none;
  float: left;
}
.coupons .main .content .couponsText {
  font-size: 28px;
  width: 330px;
  height: 100%;
  float: left;
  margin: 10px 0;
  position: relative;
}
.coupons .main .content .couponsText p {
  margin: 10px 0 0 30px;
}
.coupons .main .content .couponsText .time {
  position: absolute;
  bottom: 20px;
  font-size: 20px;
  color: rgba(153, 153, 153, 1);
}
.coupons .main .content .btn {
  width: 150px;
  height: 152px;
  float: left;
  text-align: center;
  line-height: 152px;
  font-size: 28px;
  color: rgba(248, 134, 57, 1);
}
</style>

