<template>
  <div >
    <van-nav-bar
      title="积分兑换"
      right-text="领券中心"
      fixed
      left-arrow
      @click-left="$toPage(-1)"
      @click-right="$toPage('toCoupons')"
    />
    <div class="exchange_bg">
      <img src="../../assets/me_icon/exchange_bg.png" alt="">
    </div>
    <div>
      <exchange :exchanges="exchanges" @exchangeFlag="exchangeFlag" :btn="'立即兑换'"/>
    </div>    
  </div>
</template>
<script>
import exchange from '@/components/exchangeAssembly.vue'
export default {
  data() {
    return {
      exchanges:[],
      count: 0,
    };
  },
  //引组件
  components: {
    exchange
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo();
    }
  },
  //页面打开时调用
  mounted() {
    this.getExchanges()
  },
  methods: {
    getExchanges(){
      JNB.query({
        serverName:'discountServer',
        cls:'CouponManageModule',
        method: 'queryCouponExcgPub',
        params: {
          compid: this.userInfo.comp.storeId
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            // 更新用户登录状态
            this.exchanges = result.data
            //判断是否存在积分能够兑换的优惠券
            if(this.exchanges.length === 0){
              this.$toast('未查询到优惠券哦！')
            }

            this.exchanges.forEach( (data) => {
              data.ladderVOS[0].offers = data.ladderVOS[0].offer
              if(data.brulecode === 2110){
                data.reduce = '减'
                data.symbol = '元'
              }else if(data.brulecode === 2120) {
                data.reduce = '包邮'
                data.ladderVOS[0].offers = ''
              }else {
                data.reduce = ''
                data.symbol = '折'
              }
            })
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    },
    exchangeFlag() {
      this.getExchanges()
    }
  }
};
</script>
<style scoped>
  img{
    width: 100%;
    height: 368px;
  }
  .van-nav-bar{
    background-color:transparent;
  }
  .van-hairline--bottom:after{
    border-bottom: 0px;
  }
  .van-nav-bar .van-icon{
    color: #ffffff;
  }
  .van-nav-bar__title{
    color: #ffffff;
  }
  .van-nav-bar__text{
    color: #ffffff;
  }
</style>
